/* Global Variables */
:root {
  --primary-color: #2270d6;
  --secondary-color: #337ab7;
  --third-color: #d9534f;
  --primary-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

/* Elements Styling */
body {
  margin: 0;
  padding: 0;
  font-family: Roboto,"Helvetica Neue Light","Helvetica Neue",Helvetica,Arial,"Lucida Grande",sans-serif;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
    background: var(--primary-color);
}

.Select {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.Select--title {
    font-size: 1rem;
}
.Select--Input {
    width: 20%;
    margin: 0 20px;
}
.ProductList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.ProductItem {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    min-height: 300px;
    margin: 20px 0;
}
.ProductItem--card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
